/**
 * get the element position relative to the viewport 
 * and get the parent element position relative to the viewport
 * using translate to move the parent element so the element is in the center of the viewport
 * and also should consider the previous translate of parent
 * element is svg node
 * parent is also svg node but in d3 format
 * @param {*} element 
 */

export function focusOnElement(element, parent) {
    
    var rect = element.getBoundingClientRect();
    var parent_rect = parent.node().getBoundingClientRect();
    var dx = (parent_rect.width - rect.width) / 2 - (rect.left - parent_rect.left);
    var dy = (parent_rect.height - rect.height) / 2 - (rect.top - parent_rect.top);
    
    // get the translate from parent
    // var translate = parent.attr("transform").replace("translate(", "").replace(")", "").split(",").map(d => parseInt(d));
    // dx += translate[0];
    // dy += translate[1];
    parent.attr("transform",`translate(${dx}, ${dy})`);
}