import React,{useEffect, useState} from 'react' 
import {tree} from 'd3'
import * as d3 from 'd3'
import { GatsbyImage } from "gatsby-plugin-image"

import {isMobile, isTablet, isDeskTop} from '../../client/utils/device'
import {randomString} from '../../client/utils/random'
import { focusOnElement } from '../../client/utils/position'
import * as styles from './styles.module.css'
import Navbar from '../../components/Navbar'

function drawD3Tree({
    ref, 
    data,
    width = 1000,
    height = 1000,
    radius = 60,
    fontSize = "1.5em",
    redundent = 4,
    padding = 0,
    nodeSize = [radius*4, radius*4],
    separation = (a, b) => (a.parent == b.parent ? 2 : 1),
}){
    var root  = d3.hierarchy(data);
    root = tree().size([width,height])
            .nodeSize(nodeSize)
            .separation(separation)(root);
    //center the tree
    let x0 = Infinity;
    let x1 = -x0;
    let y0 = Infinity;
    let y1 = -y0;
    console.log(root)
    var H = window.innerHeight;
    var W = window.innerWidth;
    root.each(d => {
        if (d.x > x1) x1 = d.x;
        if (d.x < x0) x0 = d.x;
        if (d.y > y1) y1 = d.y;
        if (d.y < y0) y0 = d.y;
        if(d.data.children)d.data.children = d.data.children.length === 0? undefined : d.data.children;
        d.data.url      = d.data.path.join("/");
        
    });

    const newWidth = y1 - y0;
    const newHeight = x1 - x0;
    console.log(`x0:${x0} y0:${y0} x1:${x1} y1:${y1}`)
    console.log(`newWidth: ${newWidth}, newHeight: ${newHeight}`)
    console.log(`width: ${width}, height: ${height}`)

    const svg = d3.create("svg")
        .attr("viewBox", [
                        x0-redundent*radius-padding, 
                        y0-redundent*radius-padding,  
                        
                        newHeight+radius*redundent*2 + padding * 2,
                        newWidth+radius*redundent*2 + padding * 2,])
        // .attr("width", width+radius*redundent*2 + padding * 2)
        // .attr("height", height+radius*redundent*2 + padding * 2)
        // .attr("style", "max-width: 100%; height: auto; height: intrinsic;")
        .attr("font-family", "sans-serif")
        .attr("font-size", 10)
        // .attr("transform",`translate(${W/2},${0})`);
    
   
    svg.append("g")
        .attr("fill", "none")
        .attr("stroke", "#ddf")
        .attr("stroke-opacity", 1.5)
        .attr("stroke-linecap", 0.4)
        .attr("stroke-width", 4)
        .selectAll("path")
        .data(root.links())
        .join("path")
            .attr("d", d3.link(d3.curveBumpX)
                .x(d => d.y)
                .y(d => d.x));

    const node = svg.append("g")
        .selectAll("a")
        .data(root.descendants())
        .join("a")
        .attr("xlink:href", d=>d.data.data? d.data.url : null)
        .attr("target", "_blank")
        .attr("transform", d => `translate(${d.y},${d.x})`)
        .attr("class",d=> d.data.data?[styles.node, styles.clickable].join(' '):styles.node)
        .attr("id", d=> d.data.id ? d.data.id : null)


    node.append("circle")
        .attr("fill", d => "#656")
        .attr("r", radius)
        .attr("stroke", d=>d.data.data?"#FFC0CB80":null)
        .attr("stroke-width", d=>d.data.data?15:null)
        .append("animate")

        

    node.append("text")
        .attr("dy", "0.5em")
        .attr("text-anchor", "middle")
        .attr("fill", "#fff")
        .attr("font-size", fontSize)
        .text((d, i) => d.data.data?d.data.data.title:d.data.name)
      

    d3.select(ref.current).append(() => svg.node());

    return svg;
}

/**
 * verticle swipe to choose among brothers and horizontal swipe right to go into child.
 * And the parents are stacked left piled up with fadede filter. 
 * Swipe left it will go to the parent
 * @param {*} param0 
 * @returns 
 */
function MobileView({pageContext, path, langKey}){
    let startPos = {x:null, y:null}
    let startTranslate = {x:null, y:null}
    let lastFocusElement = null;
    // visit pageContext.treeData and add id to each node
    const addId = (node)=>{
        node.id = randomString(64);
        if(node.children) node.children.forEach(child=>addId(child));
    }
    const addParent = (node, parent)=>{
        node.parent = parent;
        if(node.children) node.children.forEach(child=>addParent(child, node));
    }
    addId(pageContext.treeData);
    addParent(pageContext.treeData, null);
    const treeRef = React.createRef(null)

    useEffect(()=>{
        console.log(styles.focus)
        let currentNode = pageContext.treeData;
        const W = window.innerWidth;
        const H = window.innerHeight;
        const svg = drawD3Tree({
            ref: treeRef,
            data: pageContext.treeData,
            width: 400,
            height: 600,
            radius: 100,
            nodeSize: [200, 200],
            separation: (a, b) => (a.parent == b.parent ? 3 : 2),
            fontSize: "3em",
            redundent: 1,
        })
        function focus(){
            lastFocusElement && lastFocusElement.classList.remove(styles.focus);
            lastFocusElement = document.getElementById(currentNode.id);
            lastFocusElement.classList.add(styles.focus);
            focusOnElement(document.getElementById(currentNode.id), svg, styles.focus);
        }

        focus();
        window.ontouchstart = (e)=>{
            startPos = {x:e.touches[0].clientX, y:e.touches[0].clientY}
            var transform = svg.attr("transform").replace("translate(","").replace(")","").split(",").map(d=>parseInt(d));
            startTranslate = {x:transform[0], y:transform[1]}
        }
        window.ontouchmove = (e)=>{
            if(startPos.x === null || startPos.y === null) return;
            const dx = e.touches[0].clientX - startPos.x;
            const dy = e.touches[0].clientY - startPos.y;
            const x = startTranslate.x + dx;
            const y = startTranslate.y + dy;
            svg.attr("transform",`translate(${x},${y})`);
        }
        window.ontouchend = (e)=>{
            /* judge swipe direction */
            if(startPos.x === null || startPos.y === null) return;
            const dx = e.changedTouches[0].clientX - startPos.x;
            const dy = e.changedTouches[0].clientY - startPos.y;
            if(Math.abs(dx) > Math.abs(dy)){
                if(dx > 0) swipeRight();
                else swipeLeft();
            }else{
                if(dy > 0) swipeDown();
                else swipeUp();
            }
            startPos = {x:null, y:null}
            startTranslate = {x:null, y:null}
        }

        function swipeLeft(){
            if(currentNode.children){
                console.log("swipe left")
                currentNode = currentNode.children[0];
                console.log(currentNode)
            }else{
                console.log("swipe left failed")
                console.log(currentNode)
            }
            focus();
        }
        function swipeRight(){
            if(currentNode.parent){
                console.log("swipe right")
                currentNode = currentNode.parent;
                console.log(currentNode)
            }else{
                console.log("swipe right failed")
            }
            focus();
        }
        function swipeUp(){
            if(currentNode.parent){
                const parent = currentNode.parent;
                const brothers = parent.children;
                const index = brothers.indexOf(currentNode);
                if(index < brothers.length-1){
                    console.log("swipe up")
                    currentNode = brothers[index+1];
                    console.log(currentNode)
                }else{
                    console.log("swipe up failed")
                }
            }else{
                console.log("swipe up failed")
            }
            focus();
        }
        function swipeDown(){
            if(currentNode.parent){
                const parent = currentNode.parent;
                const brothers = parent.children;
                const index = brothers.indexOf(currentNode);
                if(index > 0){
                    console.log("swipe down")
                    currentNode = brothers[index-1];
                    console.log(currentNode)
                }else{
                    console.log("swipe down failed")
                }
            }else{
                console.log("swipe down failed")
            }
            focus();
        }
    })
    return(  
        <div className={[styles.container, styles.mobile].join(' ')}>
             <Navbar lang={langKey} path={path}/>
        <div ref={treeRef} className={styles.tree}/>
        <div className={styles.bgContainer}>
          <GatsbyImage image={pageContext.background.childImageSharp.gatsbyImageData} className={styles.bg}/>
        </div>
      </div>)
}
function TabletView({pageContext, path, langKey}){
    const treeRef = React.createRef(null)
    let startPos = {x:null, y:null}
    let startTranslate = {x:null, y:null}
    useEffect(()=>{ 
        const W = window.innerWidth;
        const H = window.innerHeight;
        const svg = drawD3Tree({
            ref: treeRef,
            data: pageContext.treeData,
            width: 1000,
            height: 1000,
            radius: 80,
            redundent:1,
            fontSize: "2.5em"
        })
        
        window.ontouchstart = (e)=>{
            console.log(e)
            startPos = {x:e.touches[0].clientX, y:e.touches[0].clientY}
            var transform = svg.attr("transform").replace("translate(","").replace(")","").split(",").map(d=>parseInt(d));
            console.log(transform)
            startTranslate = {x:transform[0], y:transform[1]}
        }
        window.ontouchmove = (e)=>{
            if(startPos.x === null || startPos.y === null) return;
            const dx = e.touches[0].clientX - startPos.x;
            const dy = e.touches[0].clientY - startPos.y;
            const x = startTranslate.x + dx;
            const y = startTranslate.y + dy;
            console.log(x,y)
            svg.attr("transform",`translate(${x},${y})`);
        }
        window.ontouchend = (e)=>{
            startPos = {x:null, y:null}
            startTranslate = {x:null, y:null}
        }

    },[])
    return (  
        <div className={[styles.container, styles.tablet].join(' ')}>
             <Navbar lang={langKey} path={path}/>
        <div ref={treeRef} className={styles.tree}/>
        <div className={styles.bgContainer}>
          <GatsbyImage image={pageContext.background.childImageSharp.gatsbyImageData} className={styles.bg}/>
        </div>
      </div>)
}
function DesktopView({pageContext,path, langKey}){
    const containerRef = React.createRef(null)
    const treeRef = React.createRef(null)
    const bgRef = React.createRef(null)
    const TreeDepth = 2;
    const BgDepth  = -0.2;
    const addId = (node)=>{
        node.id = randomString(64);
        if(node.children) node.children.forEach(child=>addId(child));
    }
    addId(pageContext.treeData);
    useEffect(()=>{
        var svg = drawD3Tree({
            ref: treeRef,
            data: pageContext.treeData,
            width: 2000,
            height: 2000,
            radius: 100,
            fontSize: "2em",
            redundent: 0,
            padding: 300,
            nodeSize: [300, 300],
            separation : (a, b) => (a.parent == b.parent ? 2 : 1)
        })

        function focus(){
            focusOnElement(document.getElementById(pageContext.treeData.id), svg);
        }
        focus();

        window.onmousemove = function(e){
            var x = e.clientX;
            var y = e.clientY;
            x = x - window.innerWidth/2;
            y = y - window.innerHeight/2;
            x = -x;
            y = -y;
            var treeX = TreeDepth * x;
            var treeY = TreeDepth * y;
            var bgX = BgDepth * x;
            var bgY = BgDepth * y;
            treeRef.current.style.transform = `translate(${treeX}px,${treeY}px)`;
            bgRef.current.style.transform = `translate(${bgX}px,${bgY}px)`;
        }
    })
    return (  
        <div ref={containerRef} className={[styles.container, styles.desktop].join(' ')}>
            <Navbar lang={langKey} path={path}/>
        <div ref={treeRef} className={styles.tree}/>
        <div ref={bgRef}  className={styles.bgContainer}>
          <GatsbyImage image={pageContext.background.childImageSharp.gatsbyImageData} className={styles.bg}/>
        </div>
      </div>)
}

export default function IndexPage({pageContext, path, ...others}){
    const langKey = path.split("/")[1]
    const [pageContent, setPageContent] = useState(null);
    useEffect(()=>{
        if(isDeskTop()){
            setPageContent(<DesktopView pageContext={pageContext} langKey={langKey} path={path}/>)
        }else if(isTablet()){
            setPageContent(<TabletView pageContext={pageContext} langKey={langKey} path={path}/>)
        }else(
            setPageContent(<MobileView pageContext={pageContext} langKey={langKey} path={path}/>)
        )
    },[])

    return (<>
        {pageContent}
    </>)
  }
  